import { Container, Grid } from "@mui/material";
import React, { useState, useEffect } from "react";
import GridInput from "./components/GridInput";
import Header from "./components/Header";
import Snackbar from '@mui/material/Snackbar';
import MuiAlert from '@mui/material/Alert';

const Alert = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});

function App() {
  const [count, setCount] = useState(0);
  const [chk, setChk] = React.useState(false);

  
  const [appState, setAppState] = useState({
    repos: [],
  });
  const [open, setOpen] = React.useState(false);

  const handleClick = () => {
    setOpen(true);
  };

  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }

    setOpen(false);
  };

  useEffect(() => {
    
    const interval = setInterval(() => {
      const apiUrl = "http://" + window.location.href.slice(window.location.href.indexOf("//")+2).split("/")[0] + "/rows" //window.location.href.slice(window.location.href.indexOf("//")+2).split("/")[0];
        fetch(apiUrl)
          .then((res) => res.json())
          .then((repos) => {
            setAppState({ repos: repos });
            setOpen(false)
          })
          .catch((e) => {
            setAppState({ repos: [] });
            setOpen(true)
            setChk(false)
          });
      }, 10000);  
      return () => clearInterval(interval);
  }, []);
  
  let click = function(){
    const apiUrl = "http://" + window.location.href.slice(window.location.href.indexOf("//")+2).split("/")[0] + "/rows" //window.location.href.slice(window.location.href.indexOf("//")+2).split("/")[0];
    fetch(apiUrl)
      .then((res) => res.json())
      .then((repos) => {
        setAppState({ repos: repos });
      });
  }

 
  return (
    <>
      <Header chk={chk} setChk={setChk}/>
      <Container sx={{mt: '1rem'}}>
        <GridInput repos={appState.repos} appClick={click}/>
      </Container>
      <Snackbar open={open} autoHideDuration={6000} onClose={handleClose}>
        <Alert onClose={handleClose} severity="error" sx={{ width: '100%' }}>
             !
        </Alert>
      </Snackbar>
    </>
  );
}

export default App;
