import Box from '@mui/material/Box';
import TextInput from "./TextInput";
import { TextField } from "@mui/material";
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import ruLocale from 'date-fns/locale/ru';
import DatePicker from '@mui/lab/DatePicker';
import Button from '@mui/material/Button';
import AddIcon from '@mui/icons-material/Add';
import { DateTimePicker } from '@mui/lab'; 
import * as React from 'react';


function BoxInput ({appClick, id, setId, car, setCar, placeOfLoading, 
    setPlaceOfLoading, placeOfDelivery, setPlaceOfDelivery, onPlaceOfDelivery, setOnPlaceOfDelivery,
    loading, setLoading, shipmentStart, setShipmentStart, shipmentEnd, setShipmentEnd, num, setNum, amount, setAmount, 
    titleBut, setTitleBut, editBut, ...props}){
        function titleB(){
            if(titleBut == "")
                return ""
            else
                return ""
        }
        
    return (
        <Box>
            <Box
            sx={{
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                '& > :not(style)': { m: 1, width: '35ch'}
            }}
            >
            <TextField
                {...props}
                id="car"
                value={car}
                onChange={e => setCar(e.target.value)}
                label={''} 
                variant="outlined"
                size="small"

            />
            <TextField 
                {...props}
                id="placeOfLoading"
                value={placeOfLoading}
                onChange={e => setPlaceOfLoading(e.target.value)}
                label={' '} 
                variant="outlined"
                size="small"

            />
            <TextField 
                {...props}
                id="placeOfDelivery"
                value={placeOfDelivery}
                onChange={e => setPlaceOfDelivery(e.target.value)}
                label={' '} 
                variant="outlined"
                size="small"

            />
            <TextField 
                {...props}
                id="onPlaceOfDelivery"
                value={onPlaceOfDelivery}
                onChange={e => setOnPlaceOfDelivery(e.target.value)}
                label={'  - '} 
                variant="outlined"
                size="small"

            />

            <TextField 
                {...props}
                id="loading"
                value={loading}
                onChange={e => setLoading(e.target.value)}
                label={'/'} 
                variant="outlined"
                size="small"

            />
            </Box>
            <Box 
            sx={{
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                '& > :not(style)': { m: 1, width: '35ch'}
            }}
            >
                <TextField
                    {...props}
                    id="num"
                    value={num}
                    onChange={e => setNum(e.target.value)}
                    label={' '} 
                    variant="outlined"
                    size="small"

                />
                <TextField
                    {...props}
                    id="amount"
                    value={amount}
                    onChange={e => setAmount(e.target.value)}
                    label=""
                    type="number"
                    size="small"
                />
                
                <LocalizationProvider dateAdapter={AdapterDateFns} locale={ruLocale}>
                    <DateTimePicker
                        renderInput={(props) => <TextField {...props} size="small"/>}
                        label=" "
                        value={shipmentStart}
                        mask="__/__/____ __:__"
                        onChange={(newValue) => {
                            setShipmentStart(newValue);
                        }}
                        disableMaskedInput={true}
                    />
                    <DatePicker
                        id="shEnd"
                        renderInput={(props) => <TextField {...props} size="small"/>}
                        label=" "
                        value={shipmentEnd}
                        onChange={(newValue) => {
                            setShipmentEnd(newValue);
                        }}
                        minDate={shipmentStart}
                        disableHighlightToday={true}
                        disableMaskedInput={true}
                    />
                </LocalizationProvider>
                <Box>
                    <Button 
                        title={titleBut}
                        variant="contained" 
                        endIcon={<AddIcon />} 
                        sx={{ float: 'right', boxShadow: 0 }} 
                        onClick={async () => {
                            let strShipmentEnd = "";
                            if(shipmentEnd != null){
                                let shipmentEnd1 = new Date(shipmentEnd)
                                let ddV2 = shipmentEnd1.getDate() < 10 ? "0" + String(shipmentEnd1.getDate()) : String(shipmentEnd1.getDate())
                                let mmV2 = String(1 + shipmentEnd1.getMonth()) < 10 ? "0" + String(1 + shipmentEnd1.getMonth()) : String(1 + shipmentEnd1.getMonth())
                                strShipmentEnd = String(shipmentEnd1.getFullYear()) + "-" + mmV2 + "-" + ddV2
                            }
                            let shipmentStart1 = new Date(shipmentStart)
                            let dd = shipmentStart1.getDate() < 10 ? "0" + String(shipmentStart1.getDate()) : String(shipmentStart1.getDate())
                            let mm = String(1 + shipmentStart1.getMonth()) < 10 ? "0" + String(1 + shipmentStart1.getMonth()) : String(1 + shipmentStart1.getMonth())
                            let h = shipmentStart1.getHours() < 10 ? "0" + String(shipmentStart1.getHours()) : String(shipmentStart1.getHours())
                            let m = shipmentStart1.getMinutes() < 10 ? "0" + String(shipmentStart1.getMinutes()) : String(shipmentStart1.getMinutes())
                            let strShipmentStart = String(shipmentStart1.getFullYear()) + "-" + mm + "-" + dd + " " + h + ":" + m
                            
                            let card = {
                                "id": `${id}`,
                                "car": `${car}`,
                                "placeOfLoading": `${placeOfLoading}`,
                                "placeOfDelivery": `${placeOfDelivery}`,
                                "shipmentStart": `${strShipmentStart}`, 
                                "shipmentEnd": `${strShipmentEnd}`, 
                                "loading": `${loading}`, 
                                "amount": `${amount}`,
                                "num": `${num}`,
                                "onPlaceOfDelivery": `${onPlaceOfDelivery}`,
                                }
                            if(titleBut == ""){
                              const apiUrl = "http://" + window.location.href.slice(window.location.href.indexOf("//")+2).split("/")[0] + "/form" //window.location.href.slice(window.location.href.indexOf("//")+2).split("/")[0];
                              let response = await fetch(apiUrl, {
                                        method: 'POST',
                                        mode: 'no-cors',
                                        headers: {
                                          'Content-Type': 'application/json; charset=utf-8'
                                        },
                                        body: JSON.stringify(card)
                                      });

                                    }
                            if(titleBut == ""){
                                const apiUrl = "http://" + window.location.href.slice(window.location.href.indexOf("//")+2).split("/")[0] + "/update" //window.location.href.slice(window.location.href.indexOf("//")+2).split("/")[0];
                                let response = await fetch(apiUrl, {
                                          method: 'POST',
                                          mode: 'no-cors',
                                          headers: {
                                            'Content-Type': 'application/json; charset=utf-8'
                                          },
                                          body: JSON.stringify(card)
                                        });
                             editBut()
                             setTitleBut('')
                            }
                              appClick()
                              setCar('')
                              setPlaceOfLoading('')
                              setPlaceOfDelivery('')
                              let dateTime = new Date()
                              dateTime.setHours(0,0,0,0)
                              setShipmentStart(dateTime)
                              setShipmentEnd(null)
                              setLoading('')
                              setNum('')
                              setOnPlaceOfDelivery('')
                              setAmount('')
                            }}>
                        
                    </Button>
                </Box>
            </Box>
        </Box>
        
    );
    
}
export default BoxInput