export default function EnhancedTable(props) {
  const {rows, clickCar, appClick} = props
  const [order, setOrder] = React.useState('asc');
  const [orderBy, setOrderBy] = React.useState('calories');
  const [selected, setSelected] = React.useState([]);
  const [selectElement, setSelectElement] = React.useState([]);
  const [page, setPage] = React.useState(0);
  const [dense, setDense] = React.useState(false);
  const [rowsPerPage, setRowsPerPage] = React.useState(5);

  const handleRequestSort = (event, property) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };

  const handleSelectAllClick = (event) => {
    let newSelectElement = [];
    if (event.target.checked) {
      const newSelecteds = rows.map((n) => n.id);
      document.querySelector("tbody").querySelectorAll("input").forEach(e =>{
        newSelectElement = newSelectElement.concat(selectElement,e);
      })
      setSelected(newSelecteds);
      setSelectElement(newSelectElement);
      return;
    }
    
    setSelected([]);
    setSelectElement([]);
  };



  const handleClick = (event, name) => {
    const selectedIndex = selected.indexOf(name);
    let newSelected = [];
    let newSelectElement = [];
    

    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, name);
      newSelectElement = newSelectElement.concat(selectElement,event.target);
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1));
      newSelectElement = newSelectElement.concat(selectElement.slice(1));
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1));
      newSelectElement = newSelectElement.concat(selectElement.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(
        selected.slice(0, selectedIndex),
        selected.slice(selectedIndex + 1),
      );
      newSelectElement = newSelectElement.concat(
        selectElement.slice(0, selectedIndex),
        selectElement.slice(selectedIndex + 1),
      );
    }

    setSelected(newSelected);
    setSelectElement(newSelectElement);

  };

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  const handleChangeDense = (event) => {
    setDense(event.target.checked);
  };

  const isSelected = (id) => selected.indexOf(id) !== -1;

  // Avoid a layout jump when reaching the last page with empty rows.
  const emptyRows =
    page > 0 ? Math.max(0, (1 + page) * rowsPerPage - rows.length) : 0;

  return (
    <Box sx={{ width: '100%' }}>
      <Paper sx={{ width: '100%', mb: 2 }}>
        <EnhancedTableToolbar numSelected={selected.length} selected={selected} selectElement={selectElement} setSelected={setSelected} rows={rows} clickCar={clickCar} appClick={appClick}/>
        <TableContainer>
          <Table
            sx={{ minWidth: 750 }}
            aria-labelledby="tableTitle"
            size={dense ? 'small' : 'medium'}
          >
            <EnhancedTableHead
              numSelected={selected.length}
              isSelect={selectElement}
              order={order}
              orderBy={orderBy}
              onSelectAllClick={handleSelectAllClick}
              onRequestSort={handleRequestSort}
              rowCount={rows.length}
              clickCar={clickCar}
            />
            <TableBody>
              {stableSort(rows, getComparator(order, orderBy))
                .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                .map((row, index) => {
                  const isItemSelected = isSelected(row.id);
                  const labelId = `enhanced-table-checkbox-${index}`;

                  return (
                    <TableRow
                      hover
                      role="checkbox"
                      aria-checked={isItemSelected}
                      tabIndex={-1}
                      key={row.id}
                      selected={isItemSelected}
                      style={{backgroundColor: row.col_check === 1551 ? 'rgb(163 221 166)' : row.chk_apply === 1557 ? '#ffeb3b' : row.col_check === 1553 ? '#fd9f9f': '#ffffff'}}
                    >
                      <TableCell padding="checkbox" sx={{top: 10}}>

                        <Checkbox
                          color="primary"
                          onClick={(event) => handleClick(event, row.id)}
                          checked={isItemSelected}
                          inputProps={{
                            'aria-labelledby': labelId,
                          }}
                        />
                      </TableCell>
                      <TableCell
                        id={labelId}
                        align='right'
                      >
                        <Box id={'applyID'}  position="absolute" marginLeft="-27px" style={{display: row.chk_apply === 1558 && row.numTask === null ? 'block' : 'none'}}>
                          <TaskAltIcon 
                            fontSize="small"
                            color="success"
                          />
                        </Box>
                         <Box id={'searchID'} position="absolute" marginLeft="-7px" style={{display: row.chk_search === 1554 && row.numTask === null ? 'block' : 'none'}}>
                            <SearchIcon 
                              fontSize="small"
                              color="success"
                            />
                          </Box>
                          {row.id}
                      </TableCell>
                      
                      <TableCell align='right'>{row.numTask === null ? row.num === null ? '' : ' ' + row.num : row.numTask}</TableCell>
                      <TableCell align='right'>{row.car}</TableCell>
                      <TableCell align='right'>{row.placeOfLoading}</TableCell>
                      <TableCell align='right'>{row.placeOfDelivery}<br />{row.onPlaceOfDelivery === "" ? '' : '- ' + row.onPlaceOfDelivery}</TableCell>
                      <TableCell align='right'>{row.shipmentStart}</TableCell>
                      <TableCell align='right'>{row.shipmentEnd}</TableCell>
                      <TableCell align='right'>{row.loading}</TableCell>
                      <TableCell align='right'>{row.amount}</TableCell>
                    </TableRow>
                  );
                })}
              {emptyRows > 0 && (
                <TableRow
                  style={{
                    height: (dense ? 33 : 53) * emptyRows,
                  }}
                >
                  <TableCell colSpan={6} />
                </TableRow>
              )}
            </TableBody>
          </Table>
        </TableContainer>
        <TablePagination
          rowsPerPageOptions={[5, 10, 25]}
          labelRowsPerPage="  :"
          component="div"
          count={rows.length}
          rowsPerPage={rowsPerPage}
          page={page}
          onPageChange={handleChangePage}
          onRowsPerPageChange={handleChangeRowsPerPage}
        />
      </Paper>
      <FormControlLabel
        control={<Switch checked={dense} onChange={handleChangeDense} />}
        label=" "
      />
    </Box>
  );
}
