import Grid from '@mui/material/Grid';
import { useEffect, useState } from 'react';
import BoxInput from './BoxInput';
import EnhancedTable from './EnhancedTable';
import * as React from 'react';
import MuiAlert from '@mui/material/Alert';
import Snackbar from '@mui/material/Snackbar';

    
const Alert = React.forwardRef(function Alert(props, ref) {
    return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
  });

function GridInput(props){
    let dateTime = new Date()
    dateTime.setHours(0,0,0,0)
    const [open, setOpen] = React.useState(false);
    const {repos, appClick} = props
    const [titleBut,setTitleBut] = React.useState('')
    const [id,setId] = React.useState('')
    const [car,setCar] = React.useState('')
    const [placeOfLoading, setPlaceOfLoading] = React.useState('');
    const [placeOfDelivery, setPlaceOfDelivery] = React.useState('');
    const [onPlaceOfDelivery, setOnPlaceOfDelivery] = React.useState('');
    const [num, setNum] = React.useState('');
    const [shipmentStart, setShipmentStart] = React.useState(dateTime); 
    const [shipmentEnd, setShipmentEnd] = React.useState(null);
    const [loading, setLoading] = React.useState('');
    const [amount, setAmount] = React.useState('');
    const handleClick = () => {
        setOpen(true);
      };
      const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
          return;
        }
    
        setOpen(false);
      };
    let editBut = function(){
        setTitleBut('')
    }
    let clickCar = function(selected, rows){

        if(selected.length === 1){
            setTitleBut('')
            selected.forEach(e => {
                rows.map(n => {
                if(n.id == e){
                    let yy = n.shipmentStart.split("-")[0]
                    let mm = n.shipmentStart.split("-")[1].indexOf("0") === 0 ? n.shipmentStart.split("-")[1].split("0")[1] : n.shipmentStart.split("-")[1]
                    let dd = n.shipmentStart.split("-")[2].split(" ")[0].indexOf("0") === 0 ? n.shipmentStart.split("-")[2].split(" ")[0].split("0")[1] : n.shipmentStart.split("-")[2].split(" ")[0]
                    let HH = n.shipmentStart.split(" ")[1].split(":")[0].indexOf("0") === 0 ? n.shipmentStart.split(" ")[1].split(":")[0].split("0")[1] : n.shipmentStart.split(" ")[1].split(":")[0]
                    let MM = n.shipmentStart.split(" ")[1].split(":")[1].indexOf("0") === 0 ? n.shipmentStart.split(" ")[1].split(":")[1].split("0")[1] : n.shipmentStart.split(" ")[1].split(":")[1]
                    let d = new Date(yy,mm-1,dd)
                    d.setHours(HH,MM,0,0)

                    if(n.shipmentEnd != "" && n.shipmentEnd != null){
                        let yyS = n.shipmentEnd.split("-")[0]
                        let mmS = n.shipmentEnd.split("-")[1].indexOf("0") === 0 ? n.shipmentEnd.split("-")[1].split("0")[1] : n.shipmentEnd.split("-")[1]
                        let ddS = n.shipmentEnd.split("-")[2].indexOf("0") === 0 ? n.shipmentEnd.split("-")[2].split("0")[1] : n.shipmentEnd.split("-")[2]
                        let s = new Date(yyS,mmS-1,ddS)
                        setShipmentEnd(s)
                    }
                    setId(n.id)
                    setCar(n.car)
                    setPlaceOfLoading(n.placeOfLoading)
                    setPlaceOfDelivery(n.placeOfDelivery)
                    setOnPlaceOfDelivery(n.onPlaceOfDelivery)
                    setShipmentStart(d)
                    setLoading(n.loading)
                    setAmount(n.amount)
                    let num1 = n.num === null ? '' : n.num
                    setNum(num1)
                }
                
                })
            })
        }
        else{
            handleClick()
        }

    }
    
    return (
        <Grid container spacing={2}>
           <Grid item xs={12}>
                <BoxInput appClick={appClick}
                id={id} setId={setId}
                car={car} setCar={setCar}
                placeOfLoading={placeOfLoading} setPlaceOfLoading={setPlaceOfLoading}
                placeOfDelivery={placeOfDelivery} setPlaceOfDelivery={setPlaceOfDelivery}
                loading={loading} setLoading={setLoading}
                shipmentStart={shipmentStart} setShipmentStart={setShipmentStart}
                shipmentEnd={shipmentEnd} setShipmentEnd={setShipmentEnd}
                amount={amount} setAmount={setAmount}
                onPlaceOfDelivery={onPlaceOfDelivery} setOnPlaceOfDelivery={setOnPlaceOfDelivery}
                num={num} setNum={setNum}
                titleBut={titleBut} setTitleBut={setTitleBut}
                editBut={editBut}
                />
           </Grid>
           <Grid item xs={12}>
               <EnhancedTable rows={repos} clickCar={clickCar} appClick={appClick}/>
               <Snackbar open={open} autoHideDuration={6000} onClose={handleClose}>
                    <Alert onClose={handleClose} severity="error" sx={{ width: '100%' }}>
                              !
                    </Alert>
                </Snackbar>
           </Grid>

        </Grid>
    );
}
export default GridInput