import { AppBar, Toolbar, Typography, Switch, FormGroup, FormControlLabel } from "@mui/material"
import { styled } from '@mui/material/styles';
import SearchIcon from '@mui/icons-material/Search';
import * as React from 'react';
function Header({chk, setChk}){
    const groupStyle = {
        position: 'absolute', 
        right: '10px', 
        display: 'inline-block'
    };

    const apiUrl = "http://" + window.location.href.slice(window.location.href.indexOf("//")+2).split("/")[0] + "/chksearch" //window.location.href.slice(window.location.href.indexOf("//")+2).split("/")[0];
    fetch(apiUrl)
        .then((res) => res.json())
        .then((repos) => {
          setChk(repos);
        });

    const Android12Switch = styled(Switch)(({ theme }) => ({
       
        padding: 8,
        '& .MuiSwitch-track': {
          border: '1px solid #fff',
          borderRadius: 22 / 2,
          '&:before, &:after': {
            content: '""',
            position: 'absolute',
            top: '50%',
            transform: 'translateY(-50%)',
            width: 16,
            height: 16,
          },
          '&:before': {
            backgroundImage: `url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" height="16" width="16" viewBox="0 0 24 24"><path fill="${encodeURIComponent(
              theme.palette.getContrastText(theme.palette.primary.main),
            )}" d="M21,7L9,19L3.5,13.5L4.91,12.09L9,16.17L19.59,5.59L21,7Z"/></svg>')`,
            left: 12,
          },
          '&:after': {
            backgroundImage: `url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" height="16" width="16" viewBox="0 0 24 24"><path fill="${encodeURIComponent(
              theme.palette.getContrastText(theme.palette.primary.main),
            )}" d="M19,13H5V11H19V13Z" /></svg>')`,
            right: 12,
          },
        },
        '& .MuiSwitch-thumb': {
          boxShadow: 'none',
          width: 18,
          height: 18,
          margin: 2,
          color: '#fff'
        },
      }));
    return(
        <AppBar position="static">
            <Toolbar>
                <SearchIcon
                   sx={{ fontSize: 40, mr: 1}}
                /> 
                <Typography variant="h5">
                    
                </Typography>
                <FormGroup style={groupStyle}>
                    <FormControlLabel 
                      control={<Android12Switch />} 
                      label=" "
                      checked={chk}
                      onChange={
                        async (e) => {
                          const apiUrl = "http://" + window.location.href.slice(window.location.href.indexOf("//")+2).split("/")[0] + "/start?" //window.location.href.slice(window.location.href.indexOf("//")+2).split("/")[0];
                          if(e.target.checked){
                            await fetch(apiUrl+"start=true")
                            setChk(true)
                          }
                          else{
                            await fetch(apiUrl+"start=false")
                            setChk(false)
                          }
                          }
                        }/>
                </FormGroup>
            </Toolbar>
        </AppBar>
    )
}
export default Header